var contract = require('./abi/index');

var createContract = function createContract(abi, data) {
  var factory = new contract(abi, data);
  return factory;
};

var callContract = function callContract(abi, address) {
  var factory = new contract(abi);
  factory.at(address);
  return factory;
};

module.exports = {
  createContract: createContract,
  callContract: callContract
};