module.exports = {
  InvalidParamsLength: function InvalidParamsLength(need, have) {
    return new Error('invalid params length need ' + need + ",but have " + have);
  },
  ConstructorIsNull: function ConstructorIsNull() {
    return new Error('contract consturctor not exists');
  },
  MethodNotExists: function MethodNotExists() {
    return new Error('contract method not exists');
  },
  ContractAddressIsNull: function ContractAddressIsNull() {
    return new Error('contract address can not be null');
  },
  InvalidAddressParam: function InvalidAddressParam(a) {
    return new Error('ivalid params address param=' + a);
  },
  InvalidNumberOfSolidityArgs: function InvalidNumberOfSolidityArgs() {
    return new Error('Invalid number of arguments to Solidity function');
  },
  InvalidNumberOfRPCParams: function InvalidNumberOfRPCParams() {
    return new Error('Invalid number of input parameters to RPC method');
  },
  InvalidConnection: function InvalidConnection(host) {
    return new Error('CONNECTION ERROR: Couldn\'t connect to node ' + host + '.');
  },
  InvalidProvider: function InvalidProvider() {
    return new Error('Provider not set or invalid');
  },
  InvalidResponse: function InvalidResponse(result) {
    var message = !!result && !!result.error && !!result.error.message ? result.error.message : 'Invalid JSON RPC response: ' + JSON.stringify(result);
    return new Error(message);
  },
  ConnectionTimeout: function ConnectionTimeout(ms) {
    return new Error('CONNECTION TIMEOUT: timeout of ' + ms + ' ms achived');
  }
};