function Popup() {
  window.addEventListener("message", function (event) {
    var msg = event.data;
    var cb = Popup.callbackHandler.get(msg.messageId);

    if (cb && typeof cb === "function") {
      Popup.callbackHandler.delete(msg.messageId);
      cb(msg.data);
    }
  }, false);
}

Popup.prototype.init = function (dapp, cb) {
  console.log("init:", dapp);

  if (dapp && dapp.name && dapp.contractAddress && dapp.github && dapp.author && dapp.url && dapp.logo) {
    Popup.inited = true;
    handlerMsg(method.init, dapp, cb);
  } else {
    throw new Error("DApp format error. ");
  }
};

var method = {
  init: "init",
  accountDetail: "accountDetail",
  accountList: "accountList",
  executeContract: "executeContract",
  call: "call",
  estimateGas: "estimateGas"
};
Popup.inited = false;
Popup.messageId = 0;
Popup.callbackHandler = new Map();

Popup.prototype.accountList = function (cb) {
  handlerMsg(method.accountList, {}, cb);
};

Popup.prototype.accountDetail = function (address, cb) {
  handlerMsg(method.accountDetail, {
    address: address
  }, cb);
};

Popup.prototype.executeContract = function (tx, cb) {
  handlerMsg(method.accountDetail, {
    tx: tx
  }, cb);
};

Popup.prototype.call = function (data, cb) {
  handlerMsg(method.call, data, cb);
};

Popup.prototype.estimateGas = function (data, cb) {
  handlerMsg(method.estimateGas, data, cb);
};

function handlerMsg(method, data, cb) {
  if (Popup.inited === false && method !== method.init) {
    throw new Error("Dapp not init or init failed! ");
  }

  if (!cb || typeof cb !== "function") {
    throw new Error("this method only support callback function");
  } else {
    var messageId = Popup.messageId++;
    var msg = {
      messageId: messageId,
      method: method,
      data: data
    };
    parent.postMessage(msg, '*');
    Popup.callbackHandler.set(messageId, cb);
  }
}

module.exports = Popup;